.App {
  display: flex;
  text-align: center;
  padding: 20px;
  align-items: center;
  flex-direction: column;
}

input {
  padding: 10px;
  margin: 10px;
  width: 25rem;
  font-size: 2rem;
  background-color: #E2F1E7;
  border-radius: 1.5rem;
  border: solid #387478 0.2rem;
}

button {
  padding: 10px;
  margin-top: 10px;
  background-color: #387478;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 2em;
  width: 16rem;
  border-radius: 3rem;
  border: solid #243642 0.2rem;
}

.weather-card {
  margin-top: 20px;
  padding: 20px;
  border: 2px solid #387478;
  border-radius: 10px;
  display: inline-block;
  font-weight: 500;
  color: #fff;
  background-color: #243642;
}

.weather-card h2 {
  font-size: 2.5rem;
}
.weather-card p {
  font-size: 1.5rem;
}


h1 {
  color: #243642;
  font-family: "Anton SC", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 5rem;
}

p {
  font-size: 18px;
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 3rem;
  }
  
  p {
    font-size: 16px;
  }

  input {
    width: 20rem;
    font-size: 1.5rem;
  }

  button {
    font-size: 1.5rem;
    width: 12rem;
  }

  .weather-card {
    font-size: 1.2rem;
  }

  .weather-card h2 {
    font-size: 2rem;
  }
  
  .weather-card p {
    font-size: 1.2rem;
  }

  
}
